import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    points:[],
    passed:0,                           //ÂèäÊ†ºÁ∫ø
    combo:0,                            //ËøûÂáª
    comboMax:0,                         //ËøûÂáª
    pointSize:30,                       //ÁÇπÂ§ßÂ∞è
    configShowLineLast:3,               //ÈÖçÁΩÆÈ°πÔºå‰øùÁïôÊúÄÂêé‰∏âÊù°Á∫ø
    configPassedCheckLimit:3,          //È™åËØÅÁÇπÁ∫øÁ©øËøáÂÆπÂ∑Æ 


  },
  // mutations Ë∞ÉÁî®ÊñπÂºè this.$store.commit('clearLineData',data)
  mutations: {
    //Ê∑ªÂä†ÁîªÂ∏ÉÁÇπÊï∞ÊçÆ
    addLineData(state,data){
      state.points.unshift(data)
    },
    //Ê∏ÖÁ©∫ÁîªÂ∏ÉÁÇπÊï∞ÊçÆ
    clearLineData(state){
      state.points = []
      state.passed = 0
      state.combo = 0
    },
    //ÂèäÊ†ºÁ∫øÂä†1
    addPassed(state){
      state.passed++
    },
    //comboÂä†1
    addCombo(state){
      state.combo++
      if(state.combo >= state.comboMax){
        state.comboMax = state.combo
      }
    },
    //comboÂä†1
    clearCombo(state){
      state.combo = 0
    }
  },
  // actions  Ë∞ÉÁî®ÊñπÂºè this.$store.dispatch('clearLineData',data)
  actions: { 
    //Ê∑ªÂä†ÁîªÂ∏ÉÁÇπÊï∞ÊçÆ
    addLineData (context,data) {
      context.commit('addLineData',data)
    },
    //Ê∏ÖÁ©∫ÁîªÂ∏ÉÁÇπÊï∞ÊçÆ
    clearLineData (context) {
      context.commit('clearLineData')
    }
  },
  modules: {
  },
  plugins: [createPersistedState()]
})
